import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Gallery from './views/Gallery.vue'
import Services from './views/Services.vue'

Vue.use(Router)

export default new Router({
  //mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/gallery',
      component: Gallery
    },
    {
      path: '/gallery/:id',
      component: Gallery,
      props: true
    },
    {
      path: '/services',
      component: Services,
      props: true
    }
  ]
})
